@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans&family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  background-color: #0F4C60;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  width:100vw;
  height:100vh;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

header{
  font-size: 2rem;
  color: #ffffff;
  font-family: 'Poppins', sans-serif;
}

section .container{
  display: flex;
  flex-direction: column;
  margin-top:40px;
}

section .container .row{
 display: flex;
}

footer{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}
footer span{
  font-size: 1.2em;
  color: #ffffff;
  font-family: 'Poppins', sans-serif;
}

footer button{
  border: none;
  width: 150px;
  height:60px;
  margin:20px;
  background-color: #F77431;
  color: #ffffff;
  font-size: 1.2em;
  cursor: pointer;
}

footer button:hover{
  opacity: 0.8;
}

.square{
    width:80px;
    height:80px;
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5em;
    font-weight: 500;
    color:#ffffff;
    border: 2px #ffffff solid;
}

